@import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-size: 13pt;
    font-family: 'Oswald', sans-serif;
}

.MainForm {
    padding: 10pt 100pt;
}

.headerStyle {
    width: 100%;
    height: 100%;
    align-items: center;
    text-align: center;
    padding: 30pt;
    margin: 0;
    background-position: center;
    background-size: cover;
}

.form-group {
    padding: 10px;
    margin-bottom: 2rem;
    background: rgba(192, 192, 192, 0.322);
    border-radius: .25rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.loader {
    border: 5px solid #f3f3f3; /* Light grey */
    border-top: 5px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 20px;
    height: 20px;
    display: inline-block;
    animation: spin 2s linear infinite;
    margin: 0;
    vertical-align: middle;
}

.container {
    margin: 0;
}

.loaderContainer {
    margin: 0;
    width: 40px;
    display: inline-block;
    height: 100%;
    vertical-align: middle;
}

.buttonText {
    width: 100%;
    height: 100%;
}

#enviar {
    height: 50px;
    width: auto;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media only screen and (max-width: 660px) {
    .MainForm {
        padding: 10%;
    }

    #enviar {
        width: 100%;
    }
}
  